@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');
@import url('https://fonts.cdnfonts.com/css/circe');

@media screen and (min-width: 768px) {
  .commonContainer {
    background-image: url(../../images/RegistrationPage/ellipse-tablet-purple.png),
      url(../../images/RegistrationPage/ellipse-tablet-pink.png);

    background-repeat: no-repeat, no-repeat;
    background-position: bottom left, top right;
    background-color: #e7eaf2;
  }

  .logoContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 60px;
  }

  .regImage {
    margin-bottom: 32px;
    width: 274px;
    height: 250px;
  }

  .title {
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 30px;
    line-height: 1.5;
  }

  .formContainer {
    padding: 52px 91px 52px 107px;
  }
}

/*Desktop*/
@media screen and (min-width: 1280px) {
  .commonContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  .logoContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 153px 31px 77.5px 66px;
  }

  .regImage {
    margin-bottom: 32px;
    width: 452px;
    height: 413px;
  }

  .formContainer {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 52px 91px 52px 107px;
    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(25px);
  }
}
